/*
|----------------------------------------------------------------------------
| CSS INDEX
|----------------------------------------------------------------------------
|    1. STYLESHEET
|        1.1. BODY
|        1-2. TYPOGRAPHY
|        1.3. CUSTOM
|    2. SIDEBAR
|    3. SECTIONS
|    4. WORK EXPERIENCE
|    5. INTEREST
|    6. PORTFOLIO
|    7. REFERENCES
|----------------------------------------------------------------------------
*/

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500');

* {
  font-family: 'Roboto Slab', sans-serif;
}
.fix {
  overflow: hidden;
}
p {
  font-family: 'Roboto Slab', sans-serif;
  font-weight: 300;
  font-size: 10pt;
  line-height: 17pt;
}
a {
  color: #ffffff;
}
.clear {
  clear: both;
}

/*
|----------------------------------------------------------------------------
| 1.2. BODY
|----------------------------------------------------------------------------
*/
body {
  background-color: #eae8e7;
  color: #767270;
  text-align: center;
}
.container {
  margin-top: 24px;
}
.container .progressBarContainer {
  background: rgba(255, 255, 255, 0.05);
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}
.container .progressBarContainer .progressBar {
  background: #8a0606;
  transform-origin: top left;
  transform: scale(0, 0);
}
.container .progressBarContainer,
.container .progressBarContainer .progressBar {
  height: 5px;
}

/*
|----------------------------------------------------------------------------
| 1-1. TYPOGRAPHY
|----------------------------------------------------------------------------
*/
h2,
h3 {
  font-family: 'Roboto Slab', sans-serif;
  font-weight: 500;
}
h2 {
  font-size: 22pt;
  line-height: 25pt;
  margin: 0;
}
h3 {
  font-size: 14pt;
  line-height: 24pt;
}
h4 {
  font-size: 12pt;
  line-height: 20pt;
  opacity: 1;
}
h5 {
  font-size: 10pt;
  margin: 0;
  padding: 0;
  line-height: 15pt;
}
.fa {
  font-size: 22px;
}

/*
|----------------------------------------------------------------------------
| 1.3. CUSTOM
|----------------------------------------------------------------------------
*/
.btn {
  margin-left: 0 !important;
}
.jump-line {
  margin-bottom: 20px;
}

/*
|----------------------------------------------------------------------------
| 2. SIDEBAR
|----------------------------------------------------------------------------
*/
.sidebar {
  background-color: #5da4d9;
  color: #ffffff;
  display: block;
  overflow: hidden;
  text-align: left;
}
.sidebar .heading {
  background-color: #263238;
  display: flex;
  flex-direction: column;
  height: 300px;
  justify-content: center;
  position: relative;
}
.sidebar .heading .feature-img {
  position: relative;
}
.sidebar .heading .feature-img img {
  border-radius: 50%;
  border: 4px solid #37474f;
  display: flex;
  margin: auto;
  width: 220px;
}
.sidebar .heading .button-collapse {
  display: block !important;
}
.sidebar .heading .nav-icon {
  padding: 0 16px;
  position: absolute;
  top: 0;
}
.sidebar .heading nav {
  background-color: initial;
  box-shadow: initial;
}
.sidebar .heading nav .button-collapse i {
  background-color: #5da4d9;
  font-size: 18pt;
  height: 30px;
  line-height: 24pt;
  margin-top: 20px;
  padding: 0 6px;
}
.sidebar .heading nav .side-nav h3 {
  color: #5da4d9;
  padding: 0 15px;
}
.sidebar .heading nav .side-nav li a:hover {
  color: #5da4d9;
}
.sidebar .heading nav .side-nav li:hover {
  background-color: transparent !important;
}
.sidebar .heading .title {
  text-align: center;
}
.sidebar .heading .title span {
  font-size: 10pt;
  line-height: 15pt;
  margin: -5px 0 0 0;
  padding: 0;
}
.sidebar .icon {
  margin-top: 8px;
}
.sidebar .icon .fa {
  display: inherit;
  text-align: center;
}
.sidebar .sidebar-item {
  margin-top: 10px;
  overflow: hidden;
}
.sidebar .extract {
  margin-top: 30px;
}
.sidebar .section-item-details {
  margin-bottom: 30px;
  padding-right: 26px;
}
.sidebar .professional-skills {
  margin-bottom: 20px;
  margin-top: 0;
}
.sidebar .section-item-details p {
  margin: 0;
}
.sidebar .info {
  border-bottom: 1px solid #4783c2;
  padding-left: 0;
}
.sidebar .info span {
  font-weight: 300;
  opacity: 0.7;
}
.sidebar .personal {
  margin-bottom: 20px;
}
.sidebar .no-margin {
  margin: 0;
}
.sidebar .work h4 {
  margin-bottom: 0;
}
.sidebar .address-details h4 span {
  font-size: 10pt;
}
.sidebar .skill-line {
  padding-left: 0;
  padding-right: 30px;
}
.sidebar .skill-line .progress {
  background-color: #4783c2;
  height: 2px;
  overflow: visible;
}
.sidebar .skill-line .determinate {
  background-color: #ffffff;
}
.sidebar .skill-line .determinate .fa {
  font-size: 12px;
  margin-left: 50%;
  position: absolute;
  right: 0;
  top: -5px;
}
.sidebar .skill-line h3 {
  margin-bottom: 20px;
  margin-top: 0;
}
.sidebar .skill-line span {
  opacity: initial;
}

/*
|----------------------------------------------------------------------------
| 3. SECTIONS
|----------------------------------------------------------------------------
*/
#section {
  overflow: hidden;
}
.section {
  padding-left: 32px !important;
  text-align: left;
}
.section .fa {
  display: block;
  font-size: 30pt;
  text-align: center;
}
.section .fa.left {
  font-size: 15pt;
}
.section .section-wrapper {
  background-color: #ffffff;
  margin-bottom: 24px;
  overflow: hidden;
  padding-bottom: 24px !important;
}
.section .section-wrapper .section-icon {
  margin-top: 30px;
}
.section .section-wrapper h2 {
  margin-bottom: 25px;
  margin-top: 30px;
}

/*
|----------------------------------------------------------------------------
| 4. WORK EXPERIENCE
|----------------------------------------------------------------------------
*/
.custom-content {
  padding-right: 0 !important;
}
.custom-content-wrapper {
  border-bottom: 1px solid #bdbdbd;
  margin-bottom: 25px;
  padding-right: 30px;
}
.custom-content-wrapper-small {
  width: 300px;
}
.custom-content-wrapper:last-child {
  border-bottom: initial;
}
.custom-content-wrapper h3 {
  font-size: 12pt;
  line-height: 20pt;
  margin: 0;
}
.custom-content-wrapper h3 span {
  color: #767270;
}
.custom-content-wrapper p {
  color: #000;
  font-weight: 300;
}
.custom-content-wrapper span {
  color: #5da4d9;
  font-size: 10pt;
  line-height: 15pt;
  margin-top: 0;
}
.custom-content-wrapper p,
.custom-content-wrapper ul {
  margin-bottom: 25px;
  margin-top: 0;
}
.custom-content-wrapper .course-list {
  color: #000;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 10pt;
  font-weight: 300;
  line-height: 17pt;
}

/*
|----------------------------------------------------------------------------
| 5. INTERESTS
|----------------------------------------------------------------------------
*/
.interests ul {
  padding-left: 0;
}
.interests ul li {
  display: inline-block;
  list-style-type: none;
  width: 19%;
}
.interests ul li .fa {
  color: #5da4d9;
  text-align: left;
}

/*
|----------------------------------------------------------------------------
| 6. PROFESSIONAL SKILLS
|----------------------------------------------------------------------------
*/
.skill-line .skills-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.skill-line .skills-list span {
  display: list-item;
  list-style-type: disc;
  list-style-position: inside;
}

/*
|----------------------------------------------------------------------------
| 7. REFERENCES
|----------------------------------------------------------------------------
*/
.references a {
  color: #767270;
}
.references .info .fa {
  font: normal normal normal 14px/1 FontAwesome;
  line-height: 26px;
}
.references .info .no-margin {
  margin: 0;
}
