/*
***** body  *****
*/
body {
	background-color: #eae8e7;
	color: #767270;
}

/*
***** Sidebar  *****
*/
.sidebar {
	color: #ffffff;
	background-color: #37474F;
}
.sidebar .heading nav .button-collapse i {
	background-color: #37474F;
}
.sidebar .heading nav .side-nav h3 {
	color: #37474F;
}
.sidebar .heading nav .side-nav li a:hover {
	color: #37474F;
}
.sidebar .heading nav .side-nav li:hover {
	background-color: transparent !important;
}

.sidebar .info {
	border-bottom: 1px solid #263238;
}

.sidebar .skill-line .progress {
	background-color: #263238;
}
.sidebar .skill-line .determinate {
	background-color: #ffffff;
}

/*
***** Sections *****
*/

.section .section-wrapper {
	background-color: #ffffff;
}
.section .section-wrapper .section-icon,
.section .section-wrapper h2,
.section .section-wrapper h3 {
	color: #37474F;
}

/*
***** Work Experience  *****
*/
.custom-content-wrapper {
	border-bottom: 1px solid #bdbdbd;
}

.custom-content-wrapper span {
	color: #37474F;
}

/*
*****  Interests  *****
*/

.interests ul li .fa {
	color: #37474F;
}
/*
***** Portfolio website  *****
*/

.website span a {
	color: #37474F;
}

/* ***** Contact  *****
*
*/

input[type='text']:focus:not([readonly]),
input[type='password']:focus:not([readonly]),
input[type='email']:focus:not([readonly]),
input[type='url']:focus:not([readonly]),
input[type='time']:focus:not([readonly]),
input[type='date']:focus:not([readonly]),
input[type='datetime-local']:focus:not([readonly]),
input[type='tel']:focus:not([readonly]),
input[type='number']:focus:not([readonly]),
input[type='search']:focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
	border-bottom: 1px solid #37474F;
}
input[type='text']:focus:not([readonly]) + label,
input[type='password']:focus:not([readonly]) + label,
input[type='email']:focus:not([readonly]) + label,
input[type='url']:focus:not([readonly]) + label,
input[type='time']:focus:not([readonly]) + label,
input[type='date']:focus:not([readonly]) + label,
input[type='datetime-local']:focus:not([readonly]) + label,
input[type='tel']:focus:not([readonly]) + label,
input[type='number']:focus:not([readonly]) + label,
input[type='search']:focus:not([readonly]) + label,
textarea.materialize-textarea:focus:not([readonly]) + label {
	color: #37474F;
}
.waves-me:focus {
	background-color: #37474F;
	color: #fff;
}
.btn {
	margin-left: 10px;
	background-color: #37474F;
}
.btn:hover,
.btn:focus {
	background-color: #37474F;
}

.btn.disabled {
	background-color: #37474F;
	color: #fff;
	cursor: default;
}
.btn.disabled:focus {
	background-color: #37474F;
	color: #fff;
	cursor: default;
}
.btn.disabled:hover {
	background-color: #37474F;
	color: #fff;
	cursor: default;
}
